#flutter-samples{
    color: white;
    text-align: right;
}

#flutter-samples div{
    padding-left: 50px;
}

#flutter-logo{
    width: 300px;
}

/* Mobile Adjusts */
@media (max-width: 576px){
    #flutter-samples{
        text-align: center;
    }

    #flutter-samples div{
        padding-left: 0px;
    }    

    #flutter-logo{
        margin-top: 40px;
    }
}