/* Smartphone Transitions */
@media(max-width: 576px){
  .mobile-fade-in-bottom-up-start{
    opacity: 0;
    transition: 1s;
  }
  
  .mobile-fade-in-bottom-up-end{
    opacity: 1;
    transition: 1s;
  }
  
  .mobile-fade-in-left-right-start{
    opacity: 0;
    transition: 1s;
    margin-right: 40px;
  }
  
  .mobile-fade-in-left-right-end{
    opacity: 1;
    transition: 1s;
    margin-right: 0px;
  }

  .mobile-fade-in-right-left-start{
    opacity: 0;
    transition: 1s;
    margin-left: 40px;
  }
  
  .mobile-fade-in-right-left-end{
    opacity: 1;
    transition: 1s;
    margin-left: 0px;
  }
}

/* Desktop Transitions */
@media(min-width: 576px){
  .desktop-fade-in-bottom-up-start{
    opacity: 0;
    transition: 1s;
  }
  
  .desktop-fade-in-bottom-up-end{
    opacity: 1;
    transition: 1s;
  }
  
  .desktop-fade-in-left-right-start{
    opacity: 0;
    transition: 1s;
    margin-right: 40px;
  }
  
  .desktop-fade-in-left-right-end{
    opacity: 1;
    transition: 1s;
    margin-right: 0px;
  }

  .desktop-fade-in-right-left-start{
    opacity: 0;
    transition: 1s;
    margin-left: 40px;
  }
  
  .desktop-fade-in-right-left-end{
    opacity: 1;
    transition: 1s;
    margin-left: 0px;
  }
}