#youtube-channel{
    color: white;
    text-align: left;
}

#youtube-channel div{
    padding-right: 50px;
}

#youtube-logo{
    width: 210px;
}

/* Mobile Adjusts */
@media (max-width: 576px){
    #youtube-channel{
        text-align: center;
    }

    #youtube-channel div{
        padding-right: 0px;
    }    

    #youtube-logo{
        width: 150px;
        margin-top: 20px;
    }
}