.next-section-arrow-wrapper{
    position: absolute;
    bottom: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.next-section-text{
    color: white;
    font-size: 1.4rem;
    text-align: center;
    font-style: italic;
    text-shadow: 1px 1px black;
}

.down-arrow {
    width: 32px;
}