#guitar-scale-generator{
    color: white;
    text-align: left;
}

#guitar-scale-generator div{
    padding-right: 20px;
}

#guitar-scale-generator-preview{
    height: 600px;
    width: 300px;
    border: 1px solid;
    border-radius: 40px;
}

/* Mobile Adjusts */
@media (max-width: 576px){
    #guitar-scale-generator{
        text-align: center;
    }

    #guitar-scale-generator div{
        padding-right: 0px;
    }    

    #guitar-scale-generator-preview{
        margin-top: 40px;
    }
}