#profile-picture{
  width: 50%;
  height: 50%;
  max-width: 330px;
  max-height: 330px;
  border-radius: 1000px;
}

#profile-name{
  color: white;
  font-size: 2.8rem;
  text-align: center;
  text-shadow: 3px 3px black;
  margin: 15px 0;
}

#presentation-actions{
  display: unset;
}

.presentation-button-wrapper{
  background-color: navajowhite;
  padding: 5px 15px;
  border-radius: 50px;
  cursor: pointer;
  width: unset;
  flex: unset;
}

.presentation-button-wrapper h2{
  font-size: 1.6rem;
}

.presentation-button-icon{
  border-radius: 50px;
  width: 37px;
  border: 1px solid black;
  padding: 5px;
  background-color: navajowhite;
}

#formation-button{
  padding: 0;
}

.drawer-content{
  padding: 25px;
}

.drawer-content h2{
  text-align: center;
}

.drawer-content b{
  font-family: 'Pattaya';
  font-size: 1.5rem;
}

.drawer-content li{
  margin-left: 18px;
  line-height: 40px;
}

.MuiDrawer-paper{
  width: 40vw;
  background-color: antiquewhite;
}

/* Mobile Adjusts */
@media (max-width: 576px){
  #profile-picture{
    max-width: 290px;
    max-height: 290px;
  }
  
  .presentation-button-wrapper{
    margin-left: 8.333333%;
  }

  .MuiDrawer-paper{
    width: 75vw;
  }
}