* {
  margin: 0;
  padding: 0;
}

.panel{
  min-height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

/* Mobile Adjusts */
@media (max-width: 576px){
  .panel{
    padding: 80px 0;
  }
}