#magento-2-samples{
    color: white;
    text-align: right;
}

#magento-2-samples div{
    padding-left: 50px;
}

#magento-logo{
    width: 300px;
}

/* Mobile Adjusts */
@media (max-width: 576px){
    #magento-2-samples{
        text-align: center;
    }

    #magento-2-samples div{
        padding-left: 0px;
    }    

    #magento-logo{
        margin-top: 40px;
    }
}