#react-samples{
    color: white;
    text-align: left;
}

#react-samples div{
    padding-right: 50px;
}

#react-logo{
    width: 250px;
}

/* Mobile Adjusts */
@media (max-width: 576px){
    #react-samples{
        text-align: center;
    }

    #react-samples div{
        padding-right: 0px;
    }    

    #react-logo{
        width: 150px;
        margin-top: 40px;
    }
}